import AdminLayout from '@/layouts/default/AdminLayout.vue';

const router = {
    path: '',
    component: AdminLayout,
    redirect: '/dashboard',
    children: [{
      path: '/dashboard',
      name: '/dashboard',
      component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Dashboard.vue'),
      meta: {
        title: '仪表盘',
      },
    },
    {
      path: '/analysis',
      name: '/analysis',
      component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Analysis.vue'),
      meta: {
        title: '分析页',
      },
    },
    {
      path: '/monitor',
      name: '/monitor',
      component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Monitor.vue'),
      meta: {
        title: '监控页',
      },
    },
    {
      path: '/workplace',
      name: '/workplace',
      component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/Workplace.vue'),
      meta: {
        title: '工作台',
      },
    },
    {
      path: '/about',
      name: '/about',
      component: () => import(/* webpackChunkName: "dashboard" */ '@/views/dashboard/About.vue'),
      meta: {
        title: '关于',
      },
    }],
  };

export default router;
